<template>
  <div id="app">
    <el-container>
      <!-- 顶栏 -->
      <el-header>

        <!-- 搜索栏 -->
        <el-row id="searchBar" type="flex" justify="end">
          <el-col :span="2" id="logo"><img style="width: 48px; height: 48px" src="./assets/ava.jpg"></el-col>
          <el-col :span="10" id="logoText"><font color="#BBE6D6"># </font> <el-link :underline="false" href="https://lowbattery.cn/" type="info">李同学的云音乐</el-link></el-col>
          <el-col :span="12">
            <el-input placeholder="搜索" prefix-icon="el-icon-search" v-model="searchMsg" @keyup.enter.native="searchMusic" clearable>
              <el-button slot="append" type="primary" icon="el-icon-search" @click="searchMusic"></el-button>
            </el-input>
          </el-col>
        </el-row>


      </el-header>

      <!-- 主体 -->
      <el-main>

        <!-- 搜索结果 Page 1 -->
        <transition name="el-fade-in-linear">
          <div v-show="activePage == 1">
            <el-row> <el-button size="small" icon="el-icon-back" @click="backHomePage" circle></el-button> </el-row>
            <el-row class="tableTitle">
              <el-col :span="12">音乐标题 </el-col>
              <el-col :span="5"> 歌手 </el-col>
              <el-col :span="5"> 专辑 </el-col>
            </el-row>
            <el-row
              @click.native = "musicPlay(i,item)"
              v-for="(item, i) in musicList" :key="i"
              :class="{
                'musicList_row': true,
                'musicList_click_row': i == activeRow,
                'musicList_odd_row':i != activeRow && i%2 == 1, 'musicList_even_row':i!=activeRow && i%2 == 0
              }"
            >
              <el-col :span="12"><i class="el-icon-video-play"></i> {{ item.name }} </el-col>
              <el-col :span="5"> {{ item.artists[0].name }} </el-col>
              <el-col :span="5"> {{ item.album.name}} </el-col>
            </el-row>
          </div>
        </transition>
         
         <!-- 首页 Page 0 -->
        <transition name="el-fade-in-linear">
          <div v-show="activePage == 0">

              <!-- 走马灯 -->
              <el-carousel :interval="4000" type="card" height="200px">
                <el-carousel-item v-for="(item, i) in coverList" :key="i" @click.native="mvPlay(item.id)">
                      <img style="height:200px" :src="item.cover">
                </el-carousel-item>
              </el-carousel>

              <!-- 新歌 -->
              <el-row class="h2Title"><font color="#BBE6D6"># </font> 推荐歌单 </el-row>
              <el-row type="flex" justify="space-between" style="margin-bottom:50px;">
                <el-col :span="5" v-for="(item, i) in newSongList.slice(0,4)" :key="i" @click.native="openMusicList(item.id)">
                  <el-card :body-style="{ padding: '0px' }">
                    <img :src="item.picUrl" class="musicPic">
                    <div class="cardInfo"> {{ item.name }} </div>
                  </el-card>
                </el-col>
              </el-row>
              <el-row type="flex" justify="space-between">
                <el-col :span="5" v-for="(item, i) in newSongList.slice(4,8)" :key="i">
                  <el-card :body-style="{ padding: '0px' }">
                    <img :src="item.picUrl" class="musicPic">
                    <div class="cardInfo"> {{ item.name }} </div>
                  </el-card>
                </el-col>
              </el-row>
          </div>
        </transition>

        <!-- 歌单信息 Page 2 -->
        <transition name="el-fade-in-linear">
          <div v-show="activePage == 2">
            <!-- 返回 -->
            <el-row> <el-button size="small" icon="el-icon-back" @click="activePage = 0" circle></el-button> </el-row>
            <!-- 歌单封面和简介 -->
            <el-row type="flex" :gutter="20" style="margin-bottom:30px;">
              <el-col :span="5">
                  <img :src="playListInfo.coverImgUrl" class="coverPic">
              </el-col>
              <el-col :span="15" style="line-height:30px;" class="hidden-md-and-down">
                <el-row><div class="h2Title"> <font color="#BBE6D6"># </font> {{playListInfo.name}} </div></el-row>
                <el-row style="margin-bottom:20px;"><div class="listInfoText"> {{playListInfo.description}} </div></el-row>
                <el-row>
                  <el-col :span="1" class="listInfoText"> tags:</el-col>
                  <el-col :span="3" v-for="item in playListInfo.tags" :key="item" style="text-align: center;">
                    <el-button size="small" round> {{ item }} </el-button>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>

            <!-- 歌单列表 -->
            <el-row class="tableTitle">
              <el-col :span="12">音乐标题 </el-col>
              <el-col :span="5"> 歌手 </el-col>
              <el-col :span="5"> 专辑 </el-col>
            </el-row>

            <el-row
              @click.native = "musicPlay(i,item)"
              v-for="(item, i) in playListInfo.tracks" :key="i"
              :class="{
                'musicList_row': true,
                'musicList_click_row': i == activeRow,
                'musicList_odd_row':i != activeRow && i%2 == 1, 'musicList_even_row':i!=activeRow && i%2 == 0
              }"
            >
              <el-col :span="12"><i class="el-icon-video-play"></i> {{ item.name }} </el-col>
              <el-col :span="5"> {{ item.ar[0].name }} </el-col>
              <el-col :span="5"> {{ item.al.name}} </el-col>
            </el-row>
          </div>
        </transition>

        <!-- mv播放 Page 3 -->
        <transition name="el-fade-in-linear">
          <div v-show="activePage == 3">
            <!-- 返回 -->
            <el-row> <el-button size="small" icon="el-icon-back" @click="backHomePage" circle></el-button> </el-row>
            <video ref="video" :src="mvUrl" controls="controls" autoplay="autoplay" class="videoPlayer"></video>
            <!-- 热门评论 -->
            <el-row> <div class="h2Title"> <font color="#BBE6D6"># </font> 热门评论 </div> </el-row>
            <el-row v-for="(item, i) in hotComments" :key="i" class ="comment">
                <el-row>
                   <el-col :span="2" style="text-align: center;"> <el-avatar size="medium" :src="item.user.avatarUrl"></el-avatar>  </el-col>
                   <el-col :span="20"> <div style="">{{ item.user.nickname }} </div> </el-col>
                   <el-col :span="2">{{item.likedCount}} <i class="fa fa-thumbs-o-up" aria-hidden="true"></i></el-col>
                </el-row>
                <el-row> {{ item.content }} </el-row>

            </el-row>
          </div>
        </transition>
      </el-main>

      <!-- 底栏 -->
      <el-footer>
        <audio ref="audio" :src="musicUrl" controls="controls" class="myAudio" autoplay loop></audio>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //信息
      searchMsg: '',  //搜索框的信息
      musicList: [],  //搜索歌曲列表
      coverList: [],  //封面列表
      musicUrl: '',   //正在播放的歌地址
      mvUrl: '',
      coverUrl: '',   //歌单封面的地址
      listCreator: [],   //歌单创作者信息
      newSongList: [],   //新歌列表
      playListInfo: {     //歌单信息
        description: '',
        tags: []
      },    
      hotComments: [],    //热评信息

      //状态
      showvideo: false,   //mv播放状态
      isMusicPlay: false, // 歌曲播放状态
      isSearching: false,   // 是否在搜索
      activePage: 0,   //  主页显示的内容
      activeRow: -1   //选择的歌曲所在行
    };
  },
  created(){
      var that = this;
      // 获取热门 mv
      this.$axios.get("/api/top/mv?limit=5").then(
        function(respone) {
          //console.log(respone);
            that.coverList = respone.data.data;
        },
        function (err) {
          console.log(err);
        }
      )

      // 获取热门歌单
      this.$axios.get("/api/personalized?limit=8").then(
        function(respone) {
          //console.log(respone);
            that.newSongList = respone.data.result;
        },
        function (err) {
          console.log(err);
        }
      )
  },
  methods: {
    //查找歌曲
    searchMusic: function() {
      var that = this;
      if(that.searchMsg == '') return ;
      this.$axios.get("/api/search?keywords=" + this.searchMsg).then(
        function(response) {
          //console.log(response);
          that.musicList = response.data.result.songs;
          const h = that.$createElement;
          that.$notify({
            title: '搜索成功',
            message: h('i', {},'一共' + that.musicList.length + '个结果'),
            type: 'success',
            position: 'bottom-right'
          });
          that.activePage = 1;
        },
        function(err) {
          console.log(err);
        }
      );
    },
    
    //播放音乐
    musicPlay: function(row,song) {
      this.activeRow = row;
      var that = this;
      //console.log(this.activeRow);
      this.$axios.get("/api/song/url?id=" + song.id).then(
        function(response) {
          console.log(response);
          that.musicUrl = response.data.data[0].url;
          if(that.musicUrl == null){
            const h = that.$createElement;
            that.$notify({
              title: '亲爱的,暂无版权',
              message: h('i', {},'换换别的歌试试吧'),
              position: 'bottom-right'
            });
          }
        },
        function(err) {
          console.log(err);
        }
      )
      that.isMusicPlay = true;
    },

    //播放MV
    mvPlay(id){
      var that = this;
      this.$axios.get("/api/mv/url?id=" + id).then(
        function(respone){
            //console.log(respone);
            that.mvUrl = respone.data.data.url;
            that.$refs.audio.pause();
            that.activePage = 3;
        },
        function(err){
          console.log(err);
        }
      );

      this.$axios.get("/api/comment/mv?id=" + id).then(
        function(respone){
            //console.log(respone);
            that.hotComments = respone.data.hotComments;
        },
        function(err){
          console.log(err);
        }
      )
    },

    //打开歌单
    openMusicList(id){
        var that = this;
        this.$axios.get("/api/playlist/detail?id=" + id).then(
        function(respone){
            console.log(respone);
            that.playListInfo = respone.data.playlist;
            that.activePage = 2;

        },
        function(err){
          console.log(err);
        }
      )
    },

    //返回首页
    backHomePage(){
      this.$refs.video.pause();
      this.activePage = 0;
    }
  }
}
</script>

